// @import "../../styles/Profile";

.ProfileContainer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 20px;
  margin-top: 20px;
  height: 100vh;

  @media (max-width:768px){
    flex-direction: column;
    justify-content: center;
  }

  .SexQContainerMenu {
    flex-basis: 20%;

    @media (max-width:768px){
        flex: none;
        width: 90%;
      }
  }

  .ProfileContainerContent {
    flex-basis: 80%;

    @media (max-width:768px){
        flex: none;
        width: 90%;
        margin-top: 0;
        margin-bottom: 20px;
      }
    .ProfileContainerForm {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 80%;
      margin: 0 auto;

      .ProfileContainerFormTitle {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 20px;
      }

      .ProfileContainerChild {
        margin-bottom: 10px;

        input {
          width: 100%;
          padding: 10px;
          border: 1px solid #ccc;
          border-radius: 4px;
          font-size: 16px;
        }
      }

      .ProfileContainerChildBtn {
        width: 100%;
        padding: 10px;
        border: none;
        border-radius: 4px;
        background-color: #2f00ff;
        color: #fff;
        font-size: 16px;
        cursor: pointer;
        transition: background-color 0.3s ease-in-out;

        &:hover {
          background-color: #14009d;
        }
      }
    }
  }
}
