.PrivacyPolicy {
    font-family: Arial, sans-serif;
    color: #333;
    max-width: 800px;
    margin: 0 auto;
    margin-top: 10px;
  
    h1 {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }
  
    h2 {
      font-size: 20px;
      font-weight: bold;
      margin-top: 40px;
      margin-bottom: 15px;
    }
  
    p {
      margin-bottom: 10px;
    }
  
    ul {
      list-style-type: disc;
      margin-left: 20px;
      margin-bottom: 15px;
    }
  
    li {
      margin-bottom: 5px;
    }
  }