.formContainer {
  display: flex;
  justify-content: center;

  .formContainer-form {
    display: flex;
    align-items: center;

    .searchinput {
      padding: 5px;
      border: 1px solid gray;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      font-size: 16px;
      width: 100%;
      margin-left: 5px;
      border-right: none;
      outline: none;
    }

    .searchBtn {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 10px;
      background-color: #333;
      color: #fff;
      border: none;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      cursor: pointer;

      &:hover {
        background-color: #0056b3;
      }

      &:focus {
        outline: none;
      }

      &svg {
        width: 16px;
        height: 16px;
      }
    }
  }
}
