.footerContainer {
    background: linear-gradient(180deg, #2e2f33 0%, #201f20 50%, #100d14 100%);
    color: #fff;
    padding: 40px;
    text-align: center;
    margin: 0 auto;
  
    .footerContainerheading {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }
  
    p {
      font-size: 14px;
      margin-bottom: 10px;
    }
  
    .footerContainerLinks {
      margin-top: 20px;
      font-size: 16px;
  
      a {
        color: #fff;
        text-decoration: none;
        margin: 0 10px;
        transition: color 0.3s ease;
  
        &:hover {
          text-decoration: underline;
          color: #f8f8f8;
        }
      }
    }
  
    animation: fadeInUp 0.5s ease;
  
    @keyframes fadeInUp {
      0% {
        opacity: 0;
        transform: translateY(20px);
      }
      100% {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
  