.CategoriesContainer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 20px;
  margin-left: 20px;
  @media (max-width: 768px) {
    margin: 0;
  }

  .CategoriesContainerfilters {
    flex: 1;
    margin-right: 40px;

    .CategoriesContainerHeading {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .CategoriesBox {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      @media (max-width: 1024px) {
        flex-direction: row;
        flex-wrap: wrap;
      }
      .ant-checkbox-wrapper {
        margin-right: 10px;
        margin-bottom: 10px;
      }
    }

    .CategoriesResetParent {
      margin-top: 20px;

      .CategoriesResetBtn {
        padding: 10px 20px;
        background-color: #f44336;
        color: #fff;
        border: none;
        border-radius: 4px;
        font-weight: bold;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: #d32f2f;
        }
      }
    }
  }

  .CategoriesVideos {
    flex: 5;

    .CategoriesVideosHeading {
      font-size: 34px;
      font-weight: 700;
      margin-bottom: 20px;
      @media (max-width: 768px) {
        margin-top: 20px;
      }
    }

    .CategoriesVideosContent {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
      padding-right: 10px;
      @media (max-width: 1024px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
        padding-left: 10px;
      }

      @media (max-width: 480px) {
        grid-template-columns: 1fr;
      }

      .CategoriesVideosCard {
        cursor: pointer;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        transition: transform 0.3s ease;

        &:hover {
          transform: scale(1.05);
        }

        .videoContent {
          position: relative;
          width: 100%;
          height: 0;
          padding-bottom: 56.25%; // 16:9 aspect ratio
          overflow: hidden;

          .videoContentimg {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 999;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          video {
            width: 100%;
            height: 100%;
          }
        }

        .CategoriesVideosCardCard-body {
          padding: 10px;
          background-color: #f5f5f5;
          border-top: 1px solid #ddd;

          .CategoriesVideosCardTitle {
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }

    .CategoriesVideosLoadmoreParent {
      display: flex;
      justify-content: center;
      margin-top: 20px;

      .CategoriesVideosLoadmoreBtn {
        padding: 10px 20px;
        background-color: #2196f3;
        color: #fff;
        border: none;
        border-radius: 4px;
        font-weight: bold;
        cursor: pointer;
        display: flex;
        align-items: center;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: #1976d2;
        }

        svg {
          margin-left: 5px;
        }
      }
    }
  }

  @media only screen and (max-width: 1024px) {
    flex-direction: column-reverse;
    align-items: center;

    .CategoriesContainerfilters {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }
}
