.videosDetails {
  margin: 0 auto;
  width: 100%;
  max-width: 1566px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  margin-top: 20px;

  .VideoDetailsContainer {
    display: flex;
    flex-direction: column;


    .VideoDetailsContainerCard {
      width: 100%;
      max-width: 1566px;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;

      .VideoDetailsContainerCardimg {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        z-index: 999;
      }

      video {
        width: 100%;
        height: 100%;
        max-height: 600px;
      }
    }
    .VideoDetails-info {
      margin-top: 20px;

      .VideoDetailsTitle {
        font-size: 24px;
        font-weight: bold;
      }

      .VideoDetailsCategory {
        font-size: 16px;
        color: #999;
      }
    }
  }
  .similarProducts {
    margin-top: 20px;

    .similarProductsHeading {
      font-size: 24px;
      font-weight: bold;
    }

    .similarProductsSubHeading {
      font-size: 16px;
      color: #999;
    }

    .videocontentcardParent {
      margin-top: 20px;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
  
      @media (max-width: 1024px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 950px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 480px) {
        grid-template-columns: 1fr;
      }
  


      .videocontentcard {
        width: 100%;
        // margin: 0 auto;
        padding: 10px;
        margin-right: 0;
        border-radius: 10px;
        background-color: #f9f9f9;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        transition: transform 0.3s ease;

        &:hover {
          transform: scale(1.05);
        }

        .videocontent {
          position: relative;
          width: 100%;
          height: 170px;

  

          .videocontentcardimg {
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            top: 0;
            left: 0;
            padding-left: 0;
            z-index: 999;
          }

          video {
            width: 100%;
            height: 100%;
          }
        }

        .videocontentcardText {
          .videocontentcardTitle {
            font-size: 16px;
            font-weight: bold;
            margin-top: 10px;
          }
        }
      }
    }
  }
}
