// @import "../variables"; // Import your SCSS variables file if you have one

.header {
  background-color: #f5f5f5;
  padding: 10px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

  .headerContainer {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0 auto;

    .headerContent {
      display: flex;
      align-items: center;
      position: relative;
      justify-content: space-around;
      width: 100%;

      .brandingWebite {
        display: flex;
        align-items: center;

        .togglerBtn {
          background-color: #fff;
          border: none;
          cursor: pointer;
          padding: 5px;
          margin-right: 5px;
          color: #333;

          svg{
            width: 25px;
            height: 30px;
            margin: 0;
            padding: 0;
          }
        }

        .brand {
          color: #333;
          font-size: 24px;
          font-weight: 700;
          text-decoration: none;
          margin-right: 10px;
        }
      }

      .headerContentUL {
        display: flex;
        justify-content: space-between;
        list-style: none;
        margin: 0;
        padding: 0;
        transition: all 0.3s ease;
        @media screen and (max-width: 768px) {
          flex-direction: column;
          top: 40px;
          left: 0;
          line-height: 2;
          padding: 10px 20px;
          position: absolute;
          background-color: #fff;
          z-index: 9999;
          width: 100%;
        }

        .headerContentUL-Li {
          margin-right: 10px;

          .headerContentUL-link {
            color: #333;
            text-decoration: none;
            padding: 5px;
            border-radius: 4px;
            transition: background-color 0.3s ease;

            &:hover {
              background-color: #ddd;
            }

            &.active {
              background-color: #333;
              color: #fff;
            }
          }
        }

        .dropdown {
          position: relative;

          .dropdown-menu {
            position: absolute;
            top: 100%;
            left: 0;
            z-index: 99999;
            background-color: #fff;
            padding: 10px;
            border-radius: 4px;
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
            display: none;

            &.show {
              display: block;
            }

            .dropdown-item {
              color: #333;
              text-decoration: none;
              display: block;
              padding: 5px;

              &:hover {
                background-color: #ddd;
              }
            }
          }

          &.show {
            .dropdown-menu {
              display: block;
            }
          }
        }
      }
    }
  }
}
