.categoryContainer {
  margin: 20px;
  width: 100%;
  max-width: 1566px;
  margin: 0 auto;

  .categoryContainerHeading {
    font-size: 32px;
    font-weight: bold;
  }

  .categoryContainerSubHeading {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #888;
  }

  .categoryContent {
    .categoryContentCardParent {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 20px;
      width: 100%;
      padding-left: 20px;
      padding-right: 20px;

      @media (max-width: 1024px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 480px) {
        grid-template-columns: 1fr;
      }

      .categoryContentCard {
        cursor: pointer;
        transition: transform 0.3s ease;
        height: 100%;
        max-height: 200px;
        width: 100%;
        margin-bottom: 50px;
        &:hover {
          transform: scale(1.05);
        }

        .categoryContentCard-img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 4px;
        }

        .categoryContentCard-body {
          padding: 10px;

          .categoryContentCardTitle {
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }
  }
}
