.SearchContainer {
  margin-top: 20px;
  padding: 0 20px;
  width: 100%;
  max-width: 1566px;
  margin: 0 auto;
  .SearchContainerHeading {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  .SearchContainerSubHeading {
    font-size: 14px;
    color: #888;
    margin-bottom: 20px;
  }

  .SearchContainerCardParent {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 480px) {
      grid-template-columns: 1fr;
    }

    .SearchContainerCard {
      cursor: pointer;
      border: 1px solid #ddd;
      border-radius: 4px;
      overflow: hidden;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
        rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      transition: transform 0.3s ease;

      &:hover {
        transform: scale(1.05);
      }

      .SearchContainerCardImgAndVideo {
        position: relative;
        height: 200px;
        width: 100%;
        .SearchContainerCardimg {
          width: 100%;
          height: 100%;
          object-fit: cover;
          position: absolute;
          top: 0;
          left: 0;
          z-index: 999;
          border: none;
        }

        video {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .SearchContainerCardbody {
        padding: 10px;

        .SearchContainerCardTitle {
          font-size: 16px;
          font-weight: bold;
          margin-bottom: 5px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .SearchContainer {
    padding: 0 10px;

    .SearchContainerCardParent {
      gap: 10px;

      .SearchContainerCard {
        .SearchContainerCardImgAndVideo {
          .SearchContainerCardimg,
          video {
            height: 150px;
          }
        }

        .SearchContainerCardbody {
          padding: 5px;
        }
      }
    }
  }
}
