.HomePageContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    width: 100%;
    max-width: 1566px;
    margin: 0 auto;

    
    .videoContentParent {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 20px;

      @media (max-width: 1024px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 480px) {
        grid-template-columns: 1fr;
      }

      .videoContentCard {
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;
        border: 1px solid #ccc;
        padding: 10px;
        
        &:hover {
          transform: scale(1.05);
        }
        
        .videoContent {
          position: relative;
          width: 100%;
          height: 0;
          padding-bottom: 56.25%;
          
          .videoContentimg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          
          video {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        
        .videoContentText {
          margin-top: 10px;
          
          .videoContentTitle {
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }
    
    .loadmoreContainer {
      display: flex;
      justify-content: center;
      margin-top: 20px;
      
      .loadmoreBtn {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px 20px;
        background-color: #f0f0f0;
        border: none;
        border-radius: 5px;
        font-size: 14px;
        font-weight: bold;
        cursor: pointer;
        transition: background-color 0.3s;
        
        svg {
          margin-left: 5px;
        }
        
        &:hover {
          background-color: #e0e0e0;
        }
      }
    }
  }
  